/* product image responsive */

@media (max-width: 1399.98px) {
  .responsive_product:nth-child(3),
  .responsive_product:nth-child(2),
  .responsive_product:nth-child(4),
  .responsive_product:nth-child(1) {
    display: block;
  }
}

@media (max-width: 991.98px) {
  .responsive_product:nth-child(3),
  .responsive_product:nth-child(2),
  .responsive_product:nth-child(1) {
    display: block;
  }
  .responsive_product:nth-child(4) {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .responsive_product:nth-child(2),
  .responsive_product:nth-child(1) {
    display: block;
  }
  .responsive_product:nth-child(3),
  .responsive_product:nth-child(4) {
    display: none;
  }
}

@media (max-width: 575.98px) {
  .responsive_product:first-child {
    display: block;
  }
  .responsive_product:nth-child(2),
  .responsive_product:nth-child(3),
  .responsive_product:nth-child(4) {
    display: none;
  }
}

/*--------------------------------------------- overwrite bootstrap-------------------------------------------- */
/* Navbar */
.nav-link {
  color: rgba(255, 255, 255, 0.8) !important;
  clip-path: polygon(10% 0, 98% 0, 89% 100%, 1% 100%) !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
  padding: 20px;
  align-items: center;
}
.navbar {
  padding-bottom: 0px !important;
}
.active_menu .nav-link,
.nav-link:hover {
  background-color: #e51515 !important;
}

.navbar-light .navbar-toggler {
  border-color: rgba(255, 255, 255, 0.4);
  padding: 1px !important;
  margin-bottom: 10px;
}
.navbar-light .navbar-toggler svg {
  color: rgba(255, 255, 255, 0.4);
  font-size: 35px;
}

.product_page_menu {
  background-color: #fff;
  border-radius: 7px;
}
.product_page_menu .nav-link {
  color: #000 !important;
}
.product_page_menu svg {
  color: #000 !important;
}
.product_page_menu .navbar {
  padding-top: 0px !important;
}

/* carousel */
.carousel-indicators [data-bs-target] {
  width: 8px !important;
  height: 8px !important;
  border-radius: 50%;
}
.signature {
  font-family: "Bonheur Royale", cursive !important;
  font-size: 50px !important;
}
.ps-relative {
  position: relative;
}

.active a {
  color: red !important;
}

/* breadcrumb */
.breadcrumb-item.active {
  color: red !important;
  cursor: pointer;
}
.breadcrumb {
  align-items: center;
}


/* over write sticky npm */
.sticky-inner-wrapper {
  position: fixed;
    transform: translate3d(0px, 0px, 0px);
    top: 0px;
    width: 100% !important;
    z-index: 1111;
    background: black;
    left: 0;
    right: 0;
    text-align: center;
}

@media (max-width: 767.98px) {
  .nav-link {
    color: rgba(255, 255, 255, 0.8) !important;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%) !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
  }
  .navbar-toggler {
    margin-bottom: 0 !important;
  }
}
@media (max-width: 575.98px) {
  .product_page_menu {
    background-color: #fff;
    border-radius: 0px;
  }
}
